import { Router } from 'itty-router';
import authRoutes from './api/auth/routes';
import { rateLimitMiddleware } from './middleware/rateLimit';
import { authMiddleware } from './middleware/auth';

const router = Router();

// Add root route handler
router.get('/', () => {
  return new Response('Universal AI Assistant API', {
    headers: { 'Content-Type': 'text/plain' }
  });
});

// Add health check endpoint
router.get('/health', () => {
  return new Response(JSON.stringify({
    status: 'healthy',
    timestamp: new Date().toISOString(),
    environment: process.env.ENVIRONMENT || 'unknown'
  }), {
    headers: { 'Content-Type': 'application/json' }
  });
});

// Add version endpoint
router.get('/version', () => {
  return new Response(JSON.stringify({
    version: '1.0.0',
    lastUpdated: '2024-02-05'
  }), {
    headers: { 'Content-Type': 'application/json' }
  });
});

// Mount auth routes
router.all('/auth/*', authRoutes.handle);

// Export worker
export default {
  async fetch(request, env, ctx) {
    try {
      // Add CORS headers
      const corsHeaders = {
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, PUT, DELETE, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type, Authorization'
      };

      // Handle OPTIONS requests
      if (request.method === 'OPTIONS') {
        return new Response(null, { headers: corsHeaders });
      }

      // Apply rate limiting
      const rateLimitResult = await rateLimitMiddleware(request, env);
      if (rateLimitResult) return rateLimitResult;

      // Apply auth middleware for API routes
      if (request.url.includes('/api/')) {
        const authResult = await authMiddleware(request, env);
        if (authResult) return authResult;
      }

      // Handle routes
      const response = await router.handle(request, env, ctx);
      
      // Add CORS headers to response
      const finalResponse = response || new Response('Not Found', { status: 404 });
      Object.entries(corsHeaders).forEach(([key, value]) => {
        finalResponse.headers.set(key, value);
      });
      
      return finalResponse;
    } catch (error) {
      return new Response(`Error: ${error.message}`, { 
        status: 500,
        headers: {
          'Content-Type': 'text/plain',
          'Access-Control-Allow-Origin': '*'
        }
      });
    }
  }
}; 